footer {
  font-family: "Lato";
  font-size: 13px;
  position: fixed;
  bottom: 0px;
  left: 11px;
  width: 100%;
  height: 25px;
  min-height: 25px;
}

#upButton {
  position: fixed;
  bottom: 35px;
  right: 40px;
  z-index: 99;
  border: 0px;
  background-color: transparent;
  height: fit-content;
  width: fit-content;
  padding: 0px;
  cursor: pointer;
}

#upButton svg circle {
  transition: fill 0.3s ease;
}

#upButton:hover svg circle {
  fill: #ffb255;
}

#settingButton {
  background-color: transparent;
  height: fit-content;
  width: fit-content;
}

#settingButton svg path {
  transition: fill 0.3s ease;
}

#settingButton:hover svg path {
  fill: #ffb255;
}

