.footer-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.zoom-container label {
  color: black !important;
  margin-top: 12px;
  max-width: 31px;
  text-align: center;
}

.zoom-container button {
  color: black;
  font-size: 30px;
  background-color: transparent;
  height: fit-content;
  width: fit-content;
  border: 0;
  padding: 11px;
  margin-bottom: 2px;
  margin-left: 2px;
  margin-right: 0px;
}

.zoom-container button:disabled {
  color: grey !important;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.site-footer {
  background: linear-gradient(180deg, rgb(239, 153, 65) 0.002649275%, rgb(221, 133, 49) 29.03%, rgb(190, 101, 18) 69.58%, rgb(171, 82, 0) 99.98%);
  height: 30px;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}